*{
  font-family: 'Poppins', sans-serif;
}


.appointment-form-main{
  padding-top: 100px;

}

.appointment-form-main h1{
  padding: 20px;;
  text-align: center;
  font-size: 38px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40); 
  -webkit-text-stroke-width: 1px; 
  -webkit-text-stroke-color: #fff; 

}

.aform-container {
  
  margin: 0 auto;
  width: 350px;
  padding: 20px;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0px 20px 60px rgba(0, 0, 0, 0.1);
  }
  
  .aform-group {
    margin-bottom: 15px;
  }
  
  .aform-group label {
    display: block;
    margin-bottom: 5px;

  }

  
  .aform-group input {
    width: 100%;
    padding: 10px;
    margin-bottom: 8px;
    border: 1px solid #999999;
    border-radius: 5px;
    box-sizing: border-box;
    font-size: 16px;
  }

  .aform-group select{
    width: 100%;
    padding: 10px;
    margin-bottom: 8px;
    border: 1px solid #999999;
    border-radius: 5px;
    box-sizing: border-box;
    font-size: 16px;
  }  
  
  .book-button {
    width: 100%;
    padding: 10px;
    background-color: #3882F6;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 18px;
  }
  
  .book-button:hover {
    background-color: #0056b3;
  }
  