.lab-report-edit-container {
    margin-left: 230px; /* Adjust according to your sidebar width */
    padding: 10px;
  }
  
  .lab-report-edit-container h1 {
    padding: 20px;
    text-align: center;
    font-size: 38px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40); 
    -webkit-text-stroke-width: 1px; 
    -webkit-text-stroke-color: #fff; 
  }
  
  .lab-report-edit-form {
    margin: 0 auto;
    width: 400px;
    padding: 20px;
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 0px 20px 60px rgba(0, 0, 0, 0.1);
  }
  
  .lab-report-edit-form input {
    margin-top: 5px;
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #999999;
    border-radius: 5px;
    box-sizing: border-box;
    font-size: 16px;
  }
  
  .save-lab-report-button {
    width: 50%;
    padding: 10px 20px;
    background-color: #3882f6;
    color: #fff;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    margin-right: 20px;
  }
  
  .save-lab-report-button:hover {
    background-color: #0056b3;
  }
  
  .cancel-lab-report-button {
    width: 50%;
    padding: 10px 45px;
    background-color: #FF3333;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 18px;
  }
  
  .cancel-lab-report-button:hover {
    background-color: #900101;
  }
  