/* Signup Container */

.signup-header{
    padding-top: 110px;
}

.signup-container {
    margin: 0 auto;
    width: 400px;
    padding: 20px;
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 0px 20px 60px rgba(0, 0, 0, 0.1);
}

/* Heading */
.signup-header h1 {
    padding: 20px;
    text-align: center;
    font-size: 38px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40); 
    -webkit-text-stroke-width: 1px; 
    -webkit-text-stroke-color: #fff; 
}

.signup-container p {
    text-align: center;
    margin-bottom: 20px;
}

/* Form Group */
.signup-form-group input {
    width: 100%;
    padding: 10px;
    margin-bottom: 13px;
    border: 1px solid #999999;
    border-radius: 5px;
    box-sizing: border-box;
    font-size: 16px;
}

/* Form Control */
.signup-form-control {
    width: 100%;
    padding: 10px;
    border: 1px solid #999999;
    border-radius: 5px;
}

/* Signup Button */
.signup-button {
    width: 100%;
    padding: 10px;
    background-color: #3882F6;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 18px;
    margin-bottom: 20px;
}

.signup-button:hover {
    background-color: #0056b3;
}

/* Login Link */
.login-btn {
    text-align: center;
}

.login-btn a {
    text-decoration: none;
    color: #007bff;
}

.login-btn a:hover {
    text-decoration: underline;
}
