.verify-acc-header{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 120px;
}

.verify-acc-header h1{
    margin-bottom: 10px;
    padding: 20px;
    text-align: left;
    font-size: 38px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40); 
    -webkit-text-stroke-width: 1px; 
    -webkit-text-stroke-color: #fff; 

}

.verify-acc-btn {
    width: 15%;
    padding: 10px 30px;
    background-color: #3882f6;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 18px;
}

.verify-acc-btn:hover {
    background-color: #0056b3;
}