
.user-list-container {
  margin-left: 230px; /* Adjust according to your sidebar width */
  padding: 30px;
}


.user-list-container h1 {
  padding: 20px;
  text-align: left;
  font-size: 38px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40); 
  -webkit-text-stroke-width: 1px; 
  -webkit-text-stroke-color: #fff; 
}

.patient-list-table{
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  padding: 20px;
  overflow:auto;
}

/* .patient-list-table:hover {
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.2);
} */

.user-list-container table {
  width: 100%;
  border-collapse: collapse;
}


.user-list-container th, .user-list-container td {
  padding: 8px;
  text-align: center;
  border-bottom: 2px solid #ddd;
}

.user-list-container th {
  border-bottom: 2px solid #000;
}

.user-list-container tbody tr:hover {
  background-color: #EFF6FF;
}

.user-list-container button {
  margin-right: 5px;
  padding: 5px 10px;
  border: none;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  background-color: #FF3333;
}

.edit-user-link{
  margin-right: 5px;
  padding: 5px 10px;
  border: none;
  background-color: #3882F6;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;

}

.user-list-container button:hover {
  background-color: #900101;
}

.user-list-container a:hover {
  background-color: #0056b3;
}

.search-container {
  margin-bottom: 10px;
}

.search-container input[type="text"] {
  width: 22%; /* Adjust the width as needed */
  padding: 5px;
  border: 1px solid #999999;
  border-radius: 5px;
  box-sizing: border-box;
  font-size: 16px;
}

.add-lab-staff-button{
    padding: 10px 30px;
    background-color: #3882f6;
    color: #fff;
    border: none;
    border-radius: 5px;
    text-decoration: none;
    font-size: 16px;
 
}





/* User Edit Css */

.user-edit-container {
  margin: 0 auto;
  width: 400px;
  padding: 20px;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0px 20px 60px rgba(0, 0, 0, 0.1);
}

.user-edit-main{
  margin-left: 250px; /* Adjust according to your sidebar width */
  padding: 50px;
}

.user-edit-main h1{
  padding: 20px;;
  text-align: center;
  font-size: 38px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40); 
  -webkit-text-stroke-width: 1px; 
  -webkit-text-stroke-color: #fff; 
}




.user-edit-container input {
  width: 100%;
  padding: 10px;
  margin-top: 5px;
  margin-bottom: 15px;
  border: 1px solid #999999;
  border-radius: 5px;
  box-sizing: border-box;
  font-size: 16px;
}


.user-edit-container .save-user-button {
  width: 50%;
  padding: 10px;
  background-color: #3882F6;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 18px;
  margin-right: 15px ;
}

.user-edit-container .save-user-button:hover {
  background-color: #0056b3;
}


.user-edit-container .cancel-user-button{
  width: 50%;
  padding: 10px 48px;
  background-color: #FF3333;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 18px;

}

.user-edit-container .cancel-user-button:hover {
  background-color: #900101;
}



