/* Appointment Update Page */
.appointment-edit-list-page {
    margin-left: 230px; /* Adjust according to your sidebar width */
    padding: 30px;
    display: flex;
    flex-direction: column;
  }
  
  /* Heading */
  .appointment-edit-list-page h1 {
    padding: 10px;
    text-align: center;
    font-size: 38px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40);
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: #fff;
  }
  
  
  /* Edit Appointment Form */
  .edit-appointment-form {
    margin: 0 auto;
    width: 400px;
    padding: 20px;
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 0px 20px 60px rgba(0, 0, 0, 0.1);
  }
  
  /* Appointment List Table */
  .appointment-list-table input,
  .appointment-list-table select {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #999999;
    border-radius: 5px;
    box-sizing: border-box;
    font-size: 16px;
    margin-top: 5px;
  }


  
  /* Save Button */
  .appointment-save-btn {
    width: 100%;
    padding: 11px 20px;
    background-color: #3882f6;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    margin-top: 10px;
  }
  
  .appointment-save-btn:hover {
    background-color: #0056b3;
  }
  
  /* Cancel Button */
  .cancel-appointment-edit-button {
    width: 100%;
    padding: 11px 20px;
    background-color: #ff3333;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    margin-top: 10px;
    text-decoration: none;
    text-align: center;
    display: inline-block;
  }
  
  .cancel-appointment-edit-button:hover {
    background-color: #900101;
  }
  