


.reset-pass-header{
    padding-top: 120px;
    display: flex;
    flex-direction: column;
    align-items: center;

}

.reset-pass-header h1{
    margin-bottom: 10px;
    padding: 20px;
    text-align: left;
    font-size: 38px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40); 
    -webkit-text-stroke-width: 1px; 
    -webkit-text-stroke-color: #fff; 

}
.reset-email-text{
    margin-bottom: 15px;
    font-size: 18px;
}


.reset-pass-container{
    margin: 0 auto;
    width: 410px;
    padding: 30px 25px;
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 0px 20px 60px rgba(0, 0, 0, 0.1);

}

.reset-pass-container input{
    width: 100%;
    padding: 10px;
    margin-bottom: 20px;
    border: 1px solid #999999;
    border-radius: 5px;
    box-sizing: border-box;
    font-size: 16px;
}

.reset-pass-submit {
    width: 60%;
    padding: 10px 30px;
    background-color: #3882f6;
    color: #fff;
    border: none;
    border-radius: 5px;
    text-decoration: none;
    font-size: 16px;
    margin-right: 20px;

  }
  
  .reset-pass-submit:hover {
    background-color: #0056b3;
  }

  .cancel-reset-button{
    width: 40%;
    padding: 10px 27px;
    
    background-color: #FF3333;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 18px;

  }

  .cancel-reset-button:hover{
    background-color: #900101;
  }

