/* InventoryCategoryAdd.css */

.inventory-category-add-container {
    margin-left: 230px; /* Adjust according to your sidebar width */
    padding: 30px;
}

.inventory-category-add-container h1 {
    margin-top: 50px;
    padding: 20px;
    text-align: center;
    font-size: 38px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40); 
    -webkit-text-stroke-width: 1px; 
    -webkit-text-stroke-color: #fff; 
}

.inventory-category-add-form {
    margin: 0 auto;
    width: 400px;
    padding: 20px;
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 0px 20px 60px rgba(0, 0, 0, 0.1);
}

.inventory-category-add-form input {
    width: 100%;
    padding: 10px;
    margin-top: 10px;
    margin-bottom: 20px;
    border: 1px solid #999999;
    border-radius: 5px;
    box-sizing: border-box;
    font-size: 16px;
}


.inventory-category-add-form button {
    width: 60%;
    padding: 10px;
    background-color: #3882F6;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 18px;
    margin-right: 21px;
}

.inventory-category-add-form button:hover {
    background-color: #0056b3;
}

.inventory-category-add-form a {
    width: 30%;
    padding: 10px 37px;
    background-color: #3882F6;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 18px;
}

.inventory-category-add-form a:hover {
    background-color: #900101;
}
