.footer {
    bottom: 0;
    width: 100%;
    margin-top: 50px;
    background-color: #0F172A;
    padding: 15px 40px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    color: #fff;
    border-radius: 16px 16px 0px 0px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);

    

}

.footer-navigation,
.footer-contact,
.footer-map {
    flex: 1;
    margin-right: 20px;
}

.footer-navigation h3,
.footer-contact h3,
.footer-map h3 {
    margin-bottom: 10px;
    color: #fff;
}

.footer-navigation ul {
    list-style-type: none;
    padding-left: 0;
}

.footer-navigation ul li {
    margin-bottom: 5px;
}

.footer-navigation ul li a {
    text-decoration: none;
    color: #fff;
}
.footer-navigation ul li a:hover{
    color: #64748B;


}

.footer-contact p {
    margin-bottom: 10px;
}

.footer-contact button{
    margin-top: 10px;
    padding: 5px 10px;
    background-color: #64748B;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;    
    text-decoration: none;
}

.footer-contact a {
    color: #fff;
}

.footer-bottom {
    flex-basis: 100%;
    text-align: center;
    margin-top: 10px;
    color: #64748B;
}
