/* UserLabReportView.css */
* {
    font-family: 'Poppins', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* UserLabReportView component styles */
.user-lab-report-view-main {
    padding-top: 100px;
    text-align: center;
}

.user-lab-report-view-main h1{
    padding: 20px;
    text-align: center;
    font-size: 38px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40);
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: #fff;
}

.user-lab-report-view-container {
    margin: 0 auto;
    max-width: 800px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
    padding: 20px;
    overflow: hidden;
}


.user-lab-report-details {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    padding: 10px 20px;
    border: 2px solid #000;
   
}

.user-report-view-heading {
    text-align: left;
}

.user-report-view-heading p {
    margin-bottom: 10px;
}

.user-lab-results h2 {
    font-size: 24px;    
    padding-bottom: 15px;
}

.user-lab-results{
    padding: 10px;
    
}


.user-lab-results table {
    width: 100%;
    border-collapse: collapse;
}

.user-lab-results th,
.user-lab-results td {
    padding: 8px;
    border-bottom: 2px solid #ddd;
    
}


.user-lab-results th {
    border-bottom: 2px solid #000;
    color: #000;
}

.report-button-div {
    display: flex;
    margin-bottom: 10px;
    justify-content: space-evenly;
    margin-top: 20px;
}

.report-button {
    padding: 10px 20px;
    background-color: #3882f6;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    
}

.report-button:hover {
    background-color: #0056b3;
}



.back-to-list-link{
    padding: 10px 20px;
    background-color: #3882f6;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    text-decoration: none;


    text-align: center;
    


}

.back-to-list-link:hover {
    background-color: #0056b3;
    
}
