@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
    text-decoration: none;
}


/* Sidebar Container */
.sidebar {
    position: fixed;
    display: flex;
    flex-direction: column;
    width: 230px;
    height: 100vh;
    background-color: #fff;
    padding-top: 10px;
    font-family: 'Poppins', sans-serif;
    border-right: 2px solid #94A3B8;
    box-shadow: 2px 0px 8px rgba(0, 0, 0, 0.1);
    
}

/* Logo */
.logo-container {
    display: flex;
    padding-bottom: 15px;
    /* border: 2px solid #94A3B8; */
}

.logo-container a {
    text-decoration: none;
    color: #000;
}

.logo-container p {
    font-family: 'Poppins', sans-serif;
    text-align: center;
    font-size: 18px;
}

.logo-container img {
    width: 100px;
    height: auto;
    margin-left: 62px;
}

.sidebar-links {
    list-style-type: none;
    padding: 0;
    margin: 0;
    width: 100%;
}

.sidebar-links li a{
    color: #000;
}

.sidebar-menu {
    display: flex;
    align-items: center;
    padding: 9px;
}

.sidebar-menu img {
    width: 35px;
    height: auto;
    margin-right: 10px;
}

.sidebar-menu a {
    color: #000;
    text-decoration: none;
}

/* Change color on hover */
.sidebar-menu:hover, .logout-link:hover {
    background-color: #EFF6FF;
    border-right: 3px solid #3882F6;
    /* box-shadow: 3px 0px 8px #3882F6; */
}

.sidebar-menu.active {
    background-color: #EFF6FF;
    border-right: 3px solid #3882F6;
    box-shadow: 2px 0px 2px #3882F6;
}

