.contact-us-container{
  padding-top: 100px;

}


.contact-title h1{
  padding: 25px;
  text-align: center;
  font-size: 38px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40); 
  -webkit-text-stroke-width: 1px; 
  -webkit-text-stroke-color: #fff; 
}

.contact-us-form {
  margin: 0 auto;
  width: 400px;
  padding: 20px;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0px 20px 60px rgba(0, 0, 0, 0.1);
}

.contact-form-row {
  margin-bottom: 20px;
}

.contact-input-field input{
  width: 100%;
  padding: 10px;
  margin-bottom: 8px;
  border: 1px solid #999999;
  border-radius: 5px;
  box-sizing: border-box;
  font-size: 16px;
}

.contact-input-field textarea{
  width: 100%;
  height: 120px;
  padding: 10px;
  margin-bottom: 8px;
  border: 1px solid #999999;
  border-radius: 5px;
  box-sizing: border-box;
  font-size: 16px;

}

.contact-input-field span {
  display: block;
  margin-bottom: 5px;
}

.contact-submit-button button {
  width: 100%;
  padding: 10px;
  background-color: #3882f6;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 18px;
}

.contact-submit-button button:hover {
  background-color: #0056b3;
}