.not-found-container {
    display: flex;
    justify-content: center;
    align-items: center;

  }
  
  .not-found-text {
    padding: 20px;
    text-align: center;
  }
  
  .not-found-heading {
    font-size: 72px;

  }
  
  .not-found-message {
    font-size: 24px;
    color: #333;
  }
  
  .not-found-link {
    display: inline-block;
    margin-top: 20px;
    padding: 10px 20px;
    background-color: #3882F6;
    color: #fff;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s ease;
    font-size: 24px;
  }
  
  .not-found-link:hover {
    background-color: #0056b3;
  }