.lab-reports-list {
  margin-left: 230px; /* Adjust according to your sidebar width */
  padding: 30px;
  
}

/* Header styling */
.lab-reports-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
}

.lab-reports-header h1 {
  text-align: left;
  font-size: 38px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40);
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #fff;
}

.create-lab-report-buttons:hover {
  background-color: #0056b3;
}

.create-lab-report-buttons{
padding: 10px 30px;
  background-color: #3882f6;
  color: #fff;
  border: none;
  border-radius: 5px;
  text-decoration: none;
  font-size: 16px;

}

.lab-reports-header a {
  
}



/* Table container styling */
.lab-report-table-container {  
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  padding: 20px;
  overflow: auto;
  
}

.lab-report-table-container table {
  width: 100%;
  border-collapse: collapse;
}

.lab-report-table-container th,
.lab-report-table-container td {
  padding: 8px;
  text-align: center;
  border-bottom: 2px solid #ddd;
}

.lab-report-table-container th {
  border-bottom: 2px solid #000;
}

.lab-report-table-container tbody tr:hover {
  background-color: #eff6ff;
}

/* Edit and Delete buttons */
.view-lab-report-link,
.edit-lab-report-link,
.delete-lab-report-button {
  margin-right: 5px;
  padding: 5px 10px;
  border: none;
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.view-lab-report-link {
  background-color: #3882f6;
}

.view-lab-report-link:hover {
  background-color: #0056b3;
}

.edit-lab-report-link {
  background-color: #6c757d;
}

.edit-lab-report-link:hover {
  background-color: #4e555b;
}

.delete-lab-report-button {
  background-color: #ff3333;
}

.delete-lab-report-button:hover {
  background-color: #900101;
}
