.inventory-category-list-main{
    margin-left: 230px; /* Adjust according to your sidebar width */
    padding: 30px;
}

.inventory-category-list-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.inventory-category-list-header h1{
    padding: 20px;
    text-align: left;
    font-size: 38px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40); 
    -webkit-text-stroke-width: 1px; 
    -webkit-text-stroke-color: #fff;
}

.add-category-button {
    padding: 10px 30px;
    background-color: #3882f6;
    color: #fff;
    border: none;
    border-radius: 5px;
    text-decoration: none;
    font-size: 16px;
    margin-right: 20px;
}

.add-category-button:hover {
    background-color: #0056b3;
}

.inventory-category-list {
    width: 100%;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
    padding: 20px;
    overflow: auto;
}

/* .inventory-category-list:hover {
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.2);
} */

.inventory-category-list tbody tr:hover {
    background-color: #eff6ff;
  }

.inventory-category-list th, .inventory-category-list td {
    padding: 8px;
    text-align: center;
    border-bottom: 2px solid #ddd;
}

.inventory-category-list table{
    width: 100%;
    border-collapse: collapse;
}

.inventory-category-list th{
    border-bottom: 2px solid #000;
}

.inventory-category-list input{
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.category-edit {
    margin-right: 5px;
    padding: 5px 10px;
    border: none;
    background-color: #3882F6;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
}

.category-delete {
    margin-right: 5px;
    padding: 5px 10px;
    border: none;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    background-color: #FF3333;
}

.category-delete:hover {
    background-color: #900101;
}

.category-edit:hover {
    background-color: #0056b3;
}
