/* CSS for the ContactUsList component */

.contact-list-main{
    margin-left: 230px; /* Adjust according to your sidebar width */
    padding: 30px;
}


.contact-list-container h1 {
    padding: 20px;
    text-align: left;
    font-size: 38px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40);
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: #fff;
}

.contact-list-table-body {
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
    padding: 20px;
    overflow:auto;

}

.contact-list-table {
  width: 100%;
  border-collapse: collapse;
  
}

.contact-list-table th,
.contact-list-table td {
  padding: 8px;
  text-align: center;
  border-bottom: 1px solid #ddd;
  
}

.contact-list-table th {
  border-bottom: 2px solid #000;
  cursor: pointer;
}

.contact-list-table tbody tr:hover {
  background-color: #EFF6FF;
}

.contact-list-delete-btn {
  background-color: #ff3333;
  color: #fff;
  border: none;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
}

.contact-list-delete-btn:hover {
  background-color: #cc0000;
}


.contact-list-table-body input {
    margin-right: 10px;
    padding: 5px;
    margin-bottom: 10px;
    border: 1px solid #999999;
    border-radius: 5px;
    box-sizing: border-box;
    font-size: 16px;
}

