/* StaffCreate.css */

.staff-create-container {
    margin-left: 230px; /* Adjust according to your sidebar width */
    padding: 10px;
}

.staff-create-container h1 {
    padding-bottom: 20px;
    text-align: center;
    font-size: 38px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40); 
    -webkit-text-stroke-width: 1px; 
    -webkit-text-stroke-color: #fff;
}

.staff-create-form {
    margin: 0 auto;
    width: 400px;
    padding: 20px;
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 0px 20px 60px rgba(0, 0, 0, 0.1);;
}

.staff-create-form label {
    display: block;
    margin-bottom: 5px;
}

.staff-create-form input,
.staff-create-form select {
    width: 100%;
    padding: 10px;
    margin-bottom: 12px;
    border: 1px solid #999999;
    border-radius: 5px;
    box-sizing: border-box;
    font-size: 16px;
}

.staff-create-form input[type="radio"] {
    margin-right: 8px;
}

.staff-create-form button {
    width: 60%;
    padding: 10px;
    background-color: #3882F6;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 18px;
    margin-right: 21px;
    margin-top: 6px;
}

.staff-create-form button:hover {
    background-color: #0056b3;
}

.staff-create-form a {
    width: 30%;
    padding: 10px 37px;
    background-color: #3882F6;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 18px;
}

.staff-create-form a:hover {
    background-color: #900101;
}
