/* Css for Lab test List */

.lab-test-list-main{
    margin-left: 230px; /* Adjust according to your sidebar width */
    padding: 30px;
}

.lab-test-list-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.lab-test-list-header h1{
    padding: 20px;
    text-align: left;
    font-size: 38px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40); 
    -webkit-text-stroke-width: 1px; 
    -webkit-text-stroke-color: #fff;
}

.add-lab-test-button {
    padding: 10px 30px;
    background-color: #3882f6;
    color: #fff;
    border: none;
    border-radius: 5px;
    text-decoration: none;
    font-size: 16px;
    margin-right: 20px;
  }
  
  .add-lab-test-button:hover {
    background-color: #0056b3;
  }

.lab-test-list {
    width: 100%;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
    padding: 20px;
    overflow: auto;
}


.lab-test-list th, .lab-test-list td {
    padding: 8px;
    text-align: center;
    border-bottom: 2px solid #ddd;
}

.lab-test-list table{
    width: 100%;
    border-collapse: collapse;
}

.lab-test-list tbody tr:hover {
    background-color: #eff6ff;
  }

.lab-test-list th{
    border-bottom: 2px solid #000;
}


.lab-test-list input{
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.lab-test-edit{
    margin-right: 5px;
    padding: 5px 10px;
    border: none;
    background-color: #3882F6;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
}

.lab-test-delete{
    margin-right: 5px;
    padding: 5px 10px;
    border: none;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    background-color: #FF3333;

}

.lab-test-delete:hover {
    background-color: #900101
}
  
.lab-test-edit:hover {
    background-color: #0056b3
}

.lab-test-add-submit{
    width: 50%;
    padding: 10px;
    background-color: #3882F6;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 18px;
    margin-right: 20px;
}

.lab-test-add-submit{
    background-color: #0056b3;
  }


.lab-test-add-back{
    width: 50%;
    padding: 10px 20px;
    background-color: #3882F6;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 18px;
}

.lab-test-add-back:hover {
    background-color: #900101;
  }