/* Inventory Add Container */
.inventory-add-container {
  margin-left: 230px; /* Adjust according to your sidebar width */
  padding: 20px;
}

/* Heading */
.inventory-add-container h1 {
  padding: 20px;
  text-align: center;
  font-size: 38px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40); 
  -webkit-text-stroke-width: 1px; 
  -webkit-text-stroke-color: #fff; 
}

/* Inventory Form */
.inventoryadd-form {
  margin: 0 auto;
  width: 400px;
  padding: 20px;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0px 20px 60px rgba(0, 0, 0, 0.1);
}


/* Inventory Input */
.inventoryadd-input {
  width: 100%;
  padding: 10px;
  margin-top: 4px;
  margin-bottom: 12px;
  border: 1px solid #999999;
  border-radius: 5px;
  box-sizing: border-box;
  font-size: 16px;
}

/* Inventory Submit Button */
.inventoryadd-submit-btn {
  width: 60%;
  padding: 10px;
  background-color: #3882F6;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 18px;
  margin-right: 21px;
}

.inventoryadd-submit-btn:hover {
  background-color: #0056b3;
}

.inventory-buttons-margin{
  margin-top: 10px;
}

.inventoryadd-form a{
  width: 50%;
  padding: 10px 37px;
  background-color: #3882F6;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 18px;
}

.inventoryadd-form a:hover {
  background-color: #900101;
}
