.inventory-category-edit-container {
    margin: 0 auto;
    width: 400px;
    padding: 20px;
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 0px 20px 60px rgba(0, 0, 0, 0.1);
}

.inventory-category-edit-main {
    margin-left: 250px; /* Adjust according to your sidebar width */
    padding: 50px;
}

.inventory-category-edit-main h1 {
    padding: 20px;
    text-align: center;
    font-size: 38px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40); 
    -webkit-text-stroke-width: 1px; 
    -webkit-text-stroke-color: #fff; 
}



.inventory-category-edit-container input{
    width: 100%;
    padding: 10px;
    margin-bottom: 15px;
    margin-top: 10px;
    border: 1px solid #999999;
    border-radius: 5px;
    box-sizing: border-box;
    font-size: 16px;
}

.inventory-category-edit-container .save-category-button {
    width: 50%;
    padding: 10px;
    background-color: #3882F6;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 18px;
    margin-right: 15px ;
}

.inventory-category-edit-container .save-category-button:hover {
    background-color: #0056b3;
}

.cancel-category-edit-button {
    width: 50%;
    padding: 10px 48px;
    background-color: #FF3333;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 18px;
}

.cancel-category-edit-button:hover {
    background-color: #900101;
}
