
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
    text-decoration: none;
}

/* Lab Report Create Container */
.lab-report-create-container {
    margin-left: 230px;
    padding: 20px;
  }


  
.lab-report-create-container h1 {
  padding: 20px;
  text-align: center;
  font-size: 38px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40); 
  -webkit-text-stroke-width: 1px; 
  -webkit-text-stroke-color: #fff; 
}
  
  .create-lab-report {
    margin: 0 auto;
    width: 400px;
    padding: 20px;
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 0px 20px 60px rgba(0, 0, 0, 0.1);
  }
  

  .create-lab-report input[type="text"],
  .create-lab-report input[type="date"],
  .create-lab-report select {
    width: 100%;
    padding: 10px;
    margin-bottom: 13px;
    border: 1px solid #999999;
    border-radius: 5px;
    box-sizing: border-box;
    font-size: 16px;
    margin-top: 5px;
  }
  

  .add-lab-test-btn{
    width: 100%;
    padding: 8px;
    background-color: #3882F6;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 18px;
    margin-right: 21px;
    margin-bottom: 5px;
  }

  .create-lab-report-btn{
    width: 65%;
    padding: 10px 37px;
    background-color: #3882F6;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    margin-right: 21px;
  }

  .back-lab-report-button{
    width: 30%;
    padding: 10px 31px;
    background-color: #3882F6;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
  }

  .back-lab-report-button:hover{
    background-color: #900101;
  }


  
  .add-lab-test-btn:hover,
  .create-lab-report-btn:hover {
    background-color: #0056b3;
  }


  .lab-report-remove{
    width: 100%;
    padding: 8px;
    background-color: #FF3333;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 18px;
    margin-right: 21px;
    margin-bottom: 5px;

  }

  .lab-report-remove:hover {
    background-color: #900101;
  }
  

  