* {
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.lab-report-view-main-container {
  margin-left: 230px; /* Adjust according to your sidebar width */
  padding: 30px;
  display: flex;
  flex-direction: column;
}

.lab-report-view-main-container h1 {
  padding: 20px;
  text-align: center;
  font-size: 38px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40);
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #fff;
}

.admin-lab-report-view-container {
  margin: 0 auto;
  width: 80%;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  padding: 20px;
  overflow: hidden;
}

.admin-lab-report-details {
  display: flex;
  justify-content: space-between;
  padding: 10px 20px;
  line-height: 2;
}

.admin-lab-results h2 {
  font-size: 24px;
  padding-bottom: 15px;
  text-align: center;
}

.admin-lab-results {
  padding: 10px;
}

.admin-lab-results table {
  width: 100%;
  border-collapse: collapse;
  text-align: center;
  margin-bottom: 20px;
}

.admin-lab-results th,
.admin-lab-results td {
  padding: 8px;
  border-bottom: 2px solid #ddd;
}

.admin-lab-results th {
  border-bottom: 2px solid #000;
  color: #000;
}

.back-to-report-list-view {
  margin: 0 auto;
  text-align: center; /* Center the content horizontally */
}

.back-report-view-list-link {
  padding: 10px 20px;
  background-color: #3882f6;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.back-report-view-list-link:hover {
  background-color: #0056b3;
}
