/* StaffList.css */

.staff-list-container {
    margin-left: 230px; /* Adjust according to your sidebar width */
    padding: 30px;
  }
  
  .staff-list-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;

  }
  
  .staff-list-header h1 {
    text-align: left;
    font-size: 38px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40); 
    -webkit-text-stroke-width: 1px; 
    -webkit-text-stroke-color: #fff; 
  }

  .staff-list-header a:hover {
    background-color: #0056b3;
  }
  
  .add-staff-button {
    padding: 10px 30px;
    background-color: #3882f6;
    color: #fff;
    border: none;
    border-radius: 5px;
    text-decoration: none;
    font-size: 16px;
  }
  
  .staff-table-container {
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
    padding: 20px;
    overflow: auto;
  }
  
  .staff-table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .staff-table th, .staff-table td {
    padding: 8px;
    text-align: center;
    border-bottom: 2px solid #ddd;
  }
  
  .staff-table th {
    border-bottom: 2px solid #000;
  }
  
  .staff-table tbody tr:hover {
    background-color: #EFF6FF;
  }
  
  .edit-staff-link {
    margin-right: 5px;
    padding: 5px 10px;
    border: none;
    background-color: #3882F6;
    color: #fff;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
  }
  
  .delete-staff-button {
    margin-right: 5px;
    padding: 5px 10px;
    border: none;
    background-color: #FF3333;
    color: #fff;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
  }
  
  .delete-staff-button:hover {
    background-color: #900101;
  }
  
  .edit-staff-link:hover {
    background-color: #0056b3;
  }
  