/* StaffEdit.css */

.staff-edit-main {
    margin-left: 250px; /* Adjust according to your sidebar width */
    padding: 50px;
  }
  
  .staff-edit-main h1 {
    padding: 20px;
    text-align: center;
    font-size: 38px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40); 
    -webkit-text-stroke-width: 1px; 
    -webkit-text-stroke-color: #fff; 
  }
  
  .staff-edit-container {
    margin: 0 auto;
    width: 400px;
    padding: 20px;
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 0px 20px 60px rgba(0, 0, 0, 0.1);
  }
  
  .staff-edit-container input {
    width: 100%;
    padding: 10px;
    margin-bottom: 15px;
    border: 1px solid #999999;
    border-radius: 5px;
    box-sizing: border-box;
    font-size: 16px;
  }
  
  .staff-edit-container select {
    width: 100%;
    padding: 10px;
    margin-bottom: 15px;
    border: 1px solid #999999;
    border-radius: 5px;
    box-sizing: border-box;
    font-size: 16px;
  }
  
  .staff-edit-container button {
    width: 50%;
    padding: 10px;
    background-color: #3882F6;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 18px;
    margin-right: 15px ;
  }
  
  .staff-edit-container button:hover {
    background-color: #0056b3;
  }
  
  .cancel-staff-button {
    width: 50%;
    padding: 10px 48px;
    background-color: #FF3333;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 18px;
  }
  
  .cancel-staff-button:hover {
    background-color: #900101;
  }

/* Error Message */
.error-message {
  color: #ff3333;
  font-size: 18px;
  text-align: center;
  margin-bottom: 10px;
}

.success-message{
  color: #0056b3;
  font-size: 18px;
  text-align: center;
  margin-bottom: 10px;
}
  