@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
    text-decoration: none;
}

.home-container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;  
}

.home-image {
    width: 1360px;
    height: 520px;
    margin-top: 10%;
    border-radius: 10px; 
    box-shadow: 0px 0px 20px rgba(50, 50, 50, 0.5);
}

.home-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 10px;     
}

.image-heading {
    position: absolute;
    top: 24%; /* Adjust as needed */
    left: 8%; /* Adjust as needed */
    font-weight: 700;
    font-size: 50px;
    text-shadow: 0 15px 4px rgba(0, 0, 0, 0.50); 
    -webkit-text-stroke-width: 2px; /* White border width */
    -webkit-text-stroke-color: #fff; /* White border color */
}


.contentbutton{
    position: absolute;
    top: 80%;
    left: 80%;
    padding: 10px;
    background-color: #3882F6;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;    
    text-decoration: none;    
}

.contentbutton:hover {
    background-color: #0056b3;
  }

.home-content {
    text-align: center;
    margin-top: 50px;
    padding-left: 10%;
    padding-right: 10%;
}

.home-content h2 {
    font-size: 38px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40); 
    -webkit-text-stroke-width: 1px; 
    -webkit-text-stroke-color: #fff;
    padding-bottom: 15px;
}

.home-content p {
    font-size: 18px;
    line-height: 1.5;
}


.benefits-container {
    display: flex;
    justify-content: space-between;
    margin-top: 5%;
    padding-left: 5.3%;
    padding-right: 5.3%;
    text-align: center;
}

.benefit {
    width: 30%;
    padding: 20px 20px;
    background-color: #fff;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
}

.benefit h3 {
    font-size: 24px;
    padding-bottom: 10px;
}
/* Our Services */

.wrapper{
    padding: 10px 10%;
}
#card-area{
    padding: 50px 0;
}
.box-area{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: 40px;
    margin-top: 20px;
    margin-bottom: 60px;
}
.box {
    border-radius: 10px;
    position: relative;
    overflow: hidden;
    background-color: #fff;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}
.box img{
    width: 100%;
    border-radius: 10px;
    display: block;
    transition: transform 0.5s;
}
.overlay {
    width: 100%;
    height: 100%; /* Set height to 100% */
    background: linear-gradient(transparent, #0F172A 58%);
    border-radius: 10px;
    position: absolute;
    left: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0 40px;
    text-align: center;
    font-size: 14px;
    transition: none; /* Remove transition */
    color: #fff;
    font-family: 'Poppins', sans-serif;
    overflow: hidden;
    overflow-y: auto;
}
.overlay h3 {
    font-weight: 500;
    margin-bottom: 5px;
    margin-top: 53%;
    font-family: 'Bebas Neue', sans-serif;
    font-size: 30px;
    letter-spacing: 2px;
    line-height: 1.5;
}
.overlay a{
    margin-top: 25px;
    padding: 5px 15px;
    background-color: #64748B;
    color: #fff;
    border: none;
    border-radius: 15px;
    cursor: pointer;
    font-size: 18px;
}

.overlay a:hover {
    background-color: #0056b3;
  }


.box:hover img{
    transform: scale(1.1);
}


.box:hover .overlay{
    height: 100%; /* Ensure overlay is always visible */
}


.services {
    margin-top: 60px;
}

.services h2 {
    font-size: 36px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40); 
    -webkit-text-stroke-width: 1px; 
    -webkit-text-stroke-color: #fff;
    text-align: center;
}

/* Locations */


.locations {
    text-align: center;
    padding-left: 3%;
    padding-right: 3%;
    
    
}

.locations h2 {
    font-size: 36px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40); 
    -webkit-text-stroke-width: 1px; 
    -webkit-text-stroke-color: #fff;
    padding-bottom: 20px;
}

.main-location {
    margin: 0 auto;
    width: 86%;
    padding: 10px;
    background-color: #fff;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    margin-bottom: 50px;
}

.main-location h3 {
    padding-bottom: 8px;
}

.main-location iframe{
    border: none;
}

.sub-locations{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(40%, 1fr));
    grid-gap: 28px;
    width: 100%;
    padding-left: 7%;
    padding-right: 7%;
}

.sub-location{    
    padding-top: 8px;
    background-color: #fff;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
}

.sub-locations iframe{
    border: none;
    padding: 10px;
    width: 100%;
    
}

