/* UserLabReportList.css */

/* Global styles */
* {
  font-family: 'Poppins', sans-serif;
}

/* UserLabReportList component styles */
.user-lab-reports-list {
  padding-top: 100px;
  height: 100%;
}

.user-lab-reports-header {
  text-align: center;
  margin-bottom: 10px;
}

.user-lab-reports-header h1 {
  padding: 20px;
  text-align: center;
  font-size: 38px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40);
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #fff;
}

.report-table-container {
  margin: auto;
  width: 80%;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  padding: 20px;
  overflow: auto;
  margin-bottom: 220px;
}

.report-table-container table {
  width: 100%;
  border-collapse: collapse;
}

.report-table-container th,
.report-table-container td {
  padding: 8px;
  text-align: center;
  border-bottom: 2px solid #ddd;
}

.report-table-container th {
  border-bottom: 2px solid #000;
}

.report-table-container tbody tr:hover {
  background-color: #eff6ff;
}

.view-lab-report-link {
  padding: 5px 20px;
  background-color: #3882f6;
  color: #fff;
  border: none;
  border-radius: 5px;
  text-decoration: none;
  font-size: 16px;
}

.view-lab-report-link:hover {
  background-color: #0056b3;
}
