.appointment-list-page {
  margin-left: 230px; /* Adjust according to your sidebar width */
  padding: 30px;
  display: flex;
  flex-direction: column;
}

.appointment-list-page h1 {
  padding: 20px;
  text-align: left;
  font-size: 38px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40);
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #fff;
}

.appointment-list-container {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  padding: 20px;
  overflow:auto;
}

.appointment-list-container table {
  width: 100%;
  border-collapse: collapse;
}

.appointment-list-container th,
.appointment-list-container td {
  padding: 8px;
  text-align: center;
  border-bottom: 2px solid #ddd;
}

.appointment-list-container th {
  border-bottom: 2px solid #000;
}

.appointment-list-container tbody tr:hover {
  background-color: #eff6ff;
}


.appointment-edit, .appointment-delete {
  margin-right: 5px;
  padding: 5px 10px;
  border: none;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.appointment-save-btn {
  margin-right: 5px;
  padding: 11px 20px;
  border: none;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  background-color: #3882f6;
} 

.appointment-cancel-btn{
  margin-right: 5px;
  padding: 11px 20px;
  border: none;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  background-color: #ff3333;
}



.edit-appointment-form{
  margin-top: 20px;
}

.edit-appointment-form h2{ 
  padding-bottom: 10px;
}

.appointment-list-table input{
  margin-right: 10px;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #999999;
  border-radius: 5px;
  box-sizing: border-box;
  font-size: 16px;
}

.appointment-list-container .appointment-edit {
  background-color: #3882f6;
}

.appointment-list-container .appointment-delete {
  background-color: #ff3333;
}

.appointment-delete:hover {
  background-color: #900101;
}

.appointment-edit:hover {
  background-color: #0056b3;
}

.appointment-edit-button{
    margin-right: 5px;
    padding: 5px 10px;
    border: none;
    background-color: #3882F6;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
}

.appointment-list-table select{
  width: 20%;
    padding: 10px;
    margin-bottom: 8px;
    border: 1px solid #999999;
    border-radius: 5px;
    box-sizing: border-box;
    font-size: 16px;
    margin-right: 10px;

}

.appointment-list-container select{
  width: 15%; 
  padding: 5px;
  margin-bottom: 8px;
  border: 1px solid #999999;
  border-radius: 5px;
  box-sizing: border-box;
  font-size: 16px;
  margin-right: 10px;

}

.appointment-list-container input[type="text"] {
  width: 17%; 
  padding: 5px;
  margin-bottom: 10px;
  border: 1px solid #999999;
  border-radius: 5px;
  box-sizing: border-box;
  font-size: 16px;
  margin-right: 10px;
}


