
.forbidden-text {
    margin-left: 230px; /* Adjust according to your sidebar width */
    padding: 60px;
    padding-top: 10%;
    text-align: left;
    

  }
  
  .forbidden-text h1 {
    font-size: 38px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40); 
    -webkit-text-stroke-width: 2px; 
    -webkit-text-stroke-color: #fff;
  }
  
  .forbidden-text span {
    padding-left: 15px;
    font-size: 24px;
    color: #999;
  }
  