.inventory-list-container {
    margin-left: 230px; /* Adjust according to your sidebar width */
    padding: 30px;
  }
  
  .inventory-list-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
  }
  
  .inventory-list-header h1 {
    text-align: left;
    font-size: 38px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40);
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: #fff;
  }
  
  .add-inventory-button {
    padding: 10px 30px;
    background-color: #3882f6;
    color: #fff;
    border: none;
    border-radius: 5px;
    text-decoration: none;
    font-size: 16px;
  }
  
  .add-inventory-button:hover {
    background-color: #0056b3;
  }
  
  .table-container-inventory {
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
    padding: 20px;
    overflow:auto;
  }
  
  .inventory-table {
    width: 100%;
    border-collapse: collapse;
  }
/* 
  .table-container-inventory:hover{
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.2);
  } */

  .inventory-table a{
    margin-right: 5px;
    padding: 5px 10px;
    border: none;
    background-color: #3882F6;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
  }

  .inventory-table a:hover{
    background-color: #0056b3;
  }
  
  .inventory-table th,
  .inventory-table td {
    padding: 8px;
    text-align: center;
    border-bottom: 2px solid #ddd;
  }
  
  .inventory-table th {
    border-bottom: 2px solid #000;
  }
  
  .inventory-table tbody tr:hover {
    background-color: #eff6ff;
  }
  
  .inventory-item-photo {
    width: 60px;
    height: auto;
  }
  
  .edit-inventory-link {
    color: #3882f6;
    text-decoration: none;
    margin-right: 5px;
  }
  
  .delete-inventory-button {
    margin-right: 5px;
    padding: 5px 10px;
    border: none;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    background-color: #FF3333;
  }
  
  .delete-inventory-button:hover {
    background-color: #900101;
  }

  /* CSS for Search Input */
.search-input {
  width: 17%; 
  padding: 5px;
  margin-bottom: 10px;
  border: 1px solid #999999;
  border-radius: 5px;
  box-sizing: border-box;
  font-size: 16px;
  margin-right: 30px;
}

/* CSS for Select Input */
.table-container-inventory select {
  width: 15%; 
  padding: 5px;
  margin-bottom: 8px;
  border: 1px solid #999999;
  border-radius: 5px;
  box-sizing: border-box;
  font-size: 16px;
  margin-right: 30px;
}

/* Styles for Checkbox Label */
.table-container-inventory label {
  font-size: 16px;

}

/* Styles for Checkbox Input */
.checkbox-expired{
  margin-left: 10px;
  cursor: pointer;
}

.warning-messages-quantity-expiry{
  display: flex;
}

.warning-expired-message{
  color: #FF3333 ;
  
}

.warning-low-quantity-message{
  color: #FF3333;
  margin-right: 20px;
}