* {
    font-family: 'Poppins', sans-serif;
  }
  
  /* UserAppointmentList component styles */
  .user-appointment-list-page {
    padding-top: 100px;
  }
  
  .user-appointment-list-page h1 {
    padding: 20px;
    text-align: center;
    font-size: 38px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40);
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: #fff;
  }
  
  .user-appointment-list-container {
    margin: auto;
    width: 80%;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
    padding: 20px;
    overflow: auto;
    margin-bottom: 220px;
  }
  
  .user-appointment-list-container table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .user-appointment-list-container th,
  .user-appointment-list-container td {
    padding: 8px;
    text-align: center;
    border-bottom: 2px solid #ddd;
  }
  
  .user-appointment-list-container th {
    border-bottom: 2px solid #000;
  }
  