* {
  font-family: 'Poppins', sans-serif;
}

.user-edit-profile-main{
  padding-top: 110px;
}

/* EditProfile component styles */
.user-profile-container {
  
  display: flex;
  justify-content: center;
}

.user-profile-header {
  width: 400px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0px 20px 60px rgba(0, 0, 0, 0.1);
  padding: 10px 20px;
}

.user-edit-profile-main h1 {
  text-align: center;
  font-size: 38px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.40);
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #fff;
  padding-bottom: 10px;
}



.user-profile-form label {
  display: block;
  margin-bottom: 5px;
}

.user-profile-form input,
.user-profile-form select {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #999999;
  border-radius: 5px;
  box-sizing: border-box;
  font-size: 16px;
}

.user-profile-form button {
  width: 100%;
  padding: 10px;
  background-color: #3882F6;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 18px;
}

.user-profile-form button:disabled {
  background-color: #999999;
  cursor: not-allowed;
}

.user-profile-form button:hover:not(:disabled) {
  background-color: #0056b3;
}
